<template>
  <div class="container">
    <div class="header">
      <span @click="back">取消</span>
      <span>扔纸条</span>
      <span @click="sure">确定</span>
    </div>
    <div class="area">
      <div>给<span>{{name}}</span>扔纸条</div>
      <div>
        <textarea name="familyAddr" maxlength="150" v-model="noteContent" id="familyAddr" rows="4" placeholder="5-60个字符,且不可以全部是数字"></textarea>
      </div>
      <div class="enter_letter">你还可以输入{{150 - noteContent.length > 0 ? 150 - noteContent.length : 0}}个字</div>
    </div>
  </div>
</template>
<script>
  import { createNamespacedHelpers } from "vuex";
  const { mapActions, mapState } = createNamespacedHelpers(
    "newsModule"
  );
  export default {
    components: {
      topic
    },
    data() {
      return {
        name: null,
        // 发送内容
        noteContent: '',
      }
    },
    methods: {
      ...mapActions([
        'SEND_NOTE'
      ]),
      back() {
        this.$router.go(-1)
      },
      // 发送信息
      sure() {
        this.SEND_NOTE({
          receiveUserId: this.$route.params.id,
          noteContent: this.noteContent
        }).then(data => {
          if (data.data.status == 'success') {
            this.$router.push({
              name: 'newsList',
              params: {
                anotherUserId: this.$route.params.id
              }
            })
          }else{
            this.$Toast({
              message: '发送失败,请稍后重试',
              position: 'middle',
              duration: 1000
            })
          }
        }).catch(err => {
          this.$Toast({
            message: '发送失败,请稍后重试',
            position: 'middle',
            duration: 1000
          })
          console.log(err)
        })
      }
    },
    created() {
      this.name = this.$route.params.name
    }
  }
</script>


<style lang="less" scoped>
  .container {
    background-color: #eee;
  } // 头部
  .header {
    display: flex;
    height: 90px;
    background-color: #fff;
    div {
      flex: 2;
      padding-left: 20px;
      height: 90px;
    }
    span {
      flex: 3.333;
      display: inline-block;
      text-align: center;
      height: 90px;
      line-height: 90px;
      &:nth-child(1) {
        color: #999;
        text-align: left;
        text-indent: 1em;
        font-size: 30px;
      }
      &:nth-child(2) {
        font-size: 35px;
      }
      &:nth-child(3) {
        text-align: right;
        padding-right: 1em;
        color: #44b336;
        box-sizing: border-box;
        font-size: 30px;
      }
    }
  } // 头部
  .area {
    padding:10px 20px;
    &>div:nth-child(1) {
      margin-bottom: 20px;
      font-size: 25px;
      &>span {
        color: #ffad4a;
        padding: 0 5px;
        font-size: 25px;
      }
    }
    &>div:nth-child(2) {
      background-color: #fff;
      textarea {
        width: 100%;
        border: none;
        outline: none;
        resize: none;
        text-indent: 1em;
        height: 200px;
        font-size: 25px;
        &::-webkit-input-placeholder {
          color: #bbb;
          font-size: 25px;
        }
      }
    }
    &>.enter_letter {
      width: 100%;
      height: 30px;
      line-height: 30px;
      background-color: #fff;
      padding-right: 20px !important;
      color: #aaa;
      text-align: right;
      box-sizing: border-box;
      font-size: 25px;
    }
  }
</style>




